<template>
  <div class="other-list">
    <p>Input :</p>
    <input type="text" v-model="dataRowItemOption.list" @change="setDataRowItemOption" class="ds-input" />
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow
      ? computed(() => props.dataRow.item.itemOption)
      : ref({
          list: 'A,B',
        });

    if (props.dataRow) {
      dataRowItemOption.value.list = dataRowItemOption.value.list.join(',');
    }

    let dataRowItemOptionList = ref({
      list: [],
    });
    dataRowItemOptionList.value.list = dataRowItemOption.value.list.replace(/[\t\s]/g, '').split(',');

    store.commit('setDataRowItemOption', {
      index: props.index,
      dataRowItemOption: dataRowItemOption.value,
    });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = () => {
      dataRowItemOptionList.value.list = dataRowItemOption.value.list.replace(/[\t\s]/g, '').split(',');
      store.commit('setDataRowItemOption', {
        index: props.index,
        dataRowItemOption: dataRowItemOptionList.value,
      });
      console.log('dataRowItemOption -->', store.getters.getDataRowItemOption(props.index));
    };

    return {
      dataRowItemOption,
      setDataRowItemOption,
    };
  },
});
</script>

